import {
    Checkbox,
    Grid,
    Header,
    Icon,
    Image,
    Menu,
    Segment,
    Sidebar,
  } from 'semantic-ui-react'

import React, { useState, useEffect } from 'react'
import '../App.css';
import { Link } from 'react-router-dom';


export const Drawer = ({visible, setVisible}) => {

    return (

        <Sidebar
        style={{background: '#0d6fa8'}}
            as={Menu}
            animation='overlay'
            icon='labeled'
            inverted
            onHide={() => setVisible(false)}
            vertical
            visible={visible}
            width='thin'
        >
            <Menu.Item as='a'>
                <Icon name='home' />
                <Link to='/todoapp/'>Home</Link>

</Menu.Item>
            <Menu.Item as='a'>
                <Icon name='sign in' />
  <Link to='/todoapp/login'>Signin</Link>
</Menu.Item>
            <Menu.Item as='a'>
                <Icon name='conversation' />
  Channels
</Menu.Item>
        </Sidebar>
    )
}
