const initialState = {
    colors: [
        'black', 'pink', 'red', 'orange', 'yellow', 'olive', 'green', 'teal', 'blue', 'violet', 'purple', 'brown'
    ],
    index: 0,
}

export function todo(state = initialState, action) {
    switch (action.type) {
        case 'CHANGE_COLOR':
            return {
                ...state,
                colors: action.changeColor

            }
        default: {
            return state;
        }
    }
}


export const ChangeColor = (value) => ({
    type: 'CHANGE_COLOR',
    changeColor: value
})