import React, { useState } from 'react'
import firebase from '../firebase'
import { Message, Form, Input, TextArea, Button, Select } from 'semantic-ui-react'
import { Fab } from '@material-ui/core';

export default function Todo({ todo, index, setCurrentId }) {

    const colors = ['red', 'orange', 'yellow', 'olive', 'green', 'teal', 'blue', 'violet', 'purple']

    const deleteTodo = () => {
        const todoRef = firebase.database().ref('Todo').child(todo.id);
        todoRef.remove();

    }


    return <div style={{ textAlign: 'center', marginTop: 30, textAlign: 'center', /* display: 'flex', justifyContent: 'center', */ }}>
   
        <Message  compact color={colors[index]}>
                <p style={{width: 200, wordWrap: 'break-word'}}>  {todo.title}</p>
           
                <Fab onClick={deleteTodo} style={{ marginLeft: 0, }} variant="extended" size="small" color="#fff" aria-label="add">
                    delete
            </Fab>
                <Fab onClick={() => {
                    setCurrentId(todo.id)
                    console.log(todo.id)
                }} style={{ marginLeft: 30, }} variant="extended" size="small" color="#fff" aria-label="add">
                    update
            </Fab>
       
        </Message>
    </div>
}