import React, { useState, useEffect } from 'react'
import firebase from '../firebase'
import Todo from './todo'

export default function TodoList({ index, setCurrentId, todoList }) {


  return <div>
    {todoList ? todoList.map((todo, id) => (
      <Todo setCurrentId={setCurrentId} index={index} todo={todo} key={id} />
    )) : ''}
  </div>
}